/* cta: container ft: feature*/
/* Logo-Hover Animation */
@keyframes iconhover {
    0%{
      transform: scale(1);
    }
    25%{
      transform: scale(1.125);
    }
    50%{
      transform: scale(1.25);
    }
    100%{
      transform: scale(1);
    }
  }
/* General Styles */
a{
    font-family: 'Hanken book';
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    text-transform: uppercase;
    color: #2d2c4c;
}
img{
    /* display: inline-block; */
    max-width: 100%;
    vertical-align: middle;
}
h1{
    font-family: "Hanken book";
    font-size: 4rem;
    text-align: center;
    margin-bottom: 0.8rem;
    font-weight: 400;
    line-height: 1.1em;
    text-transform: uppercase;
}
h2{
    font-family: "Hanken book";
    font-size: 3.6rem;
    text-transform: uppercase;
    line-height: 1.1em;
    font-weight: 400;
    color: #2d2c4c;
}
h3{
    font-family: "Hanken book";
    font-size: 2.6rem;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
    line-height: 1.3em;
}
p{
    color: #2d2c4c;
    font-size: 1.6rem;
    line-height: 1.5em;
    margin-bottom: 3.2rem;
    /* letter-spacing: -0.01em; */
}
button{
    font-family: "Hanken book";
    display: inline-block;
    border-radius: 4px;
    background-color: #3898EC;
    color: white;
    border: 0;
    cursor: pointer;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}
ul{
    list-style: none;
    text-align: center;
    display: flex;
      flex-direction: column;
      align-items: center;
}

/* Grouped Styles */
.section{
    padding-top: 4.8rem;
    padding-bottom: 3.2rem;
    line-height: 1.5em;
}
.label{
    display: inline-block;
    font-size: 1.4rem;
    line-height: 1.7em;
    background-color: #2d2c4c;
    color: #fff;
    margin-bottom: 3.2rem;
    padding: 0 0.8rem;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}
.title{
    font-family: "Hanken book";
    font-size: 1.6rem;
    color: #20a5e6;
    line-height: 1.2em;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    /* padding-bottom: 0.5rem; */
}
.container{
    padding: 0 1.6rem;
}
.section-header{
    margin-bottom: 3.2rem;
}
.subtitle{
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 1.5em;
    color: #2d2c4c;
    margin-bottom: 4.8rem;
}
.item-img{
    display: inline-block;
    height: 66px;
    margin-bottom: 3.2rem;
}
.cta-icon{
    display: inline-block;
    width: 23%;
    margin-bottom: 3.2rem;
}
.btn{
    padding: 2rem 2.4rem 1.6rem;
    border-radius: 3px;
    letter-spacing: 0.07em;
    color: white
}
.link{
    font-family: 'opensans';
    text-transform: none;
    display: block;
}
/* Footer Section */
.footer{
    padding: 3.2rem 0;
    background-color: #2d2c4c;
}
.link-footer{
    color: #fcd750;
}
.content{
    font-size: 1.6rem;
    line-height: 1.4em;
    margin-bottom: 2.4rem;
    color: #fff;
}
.content-txt{
    color: #fff;
    font-size: 1.6rem;
    margin-bottom: 1.6rem;
}
#content-title{
    font-size: 1.6rem;
    margin-bottom: 0.8rem;
}
.link-social{
    display: inline-block;
    width: 11%;
    margin: 0 0.8rem;
    margin-bottom: 3.2rem;
    /* animation: iconhover 4s; */
}
.foot{
    margin-bottom: 0;
}